import React, { useEffect, useState } from 'react';
import { StreamChat } from 'stream-chat';
import { Chat, Channel, ChannelHeader, ChannelList, LoadingIndicator, MessageInput, MessageList, Thread, Window, ChannelSearch } from 'stream-chat-react';
import queryString from 'query-string';

import 'stream-chat-react/dist/css/index.css';
const dataQuery = queryString.parse(window.location.search);

const userToken = dataQuery.token;


const filters = { type: 'messaging', members: { $in: [dataQuery.id] } };
const sort = { last_message_at: -1 };

const App = () => {

    const [chatClient, setChatClient] = useState(null);
    useEffect(() => {
        const initChat = async () => {
            const client = StreamChat.getInstance('qrjjtnn5hv29');

            await client.connectUser(
                {
                    id: dataQuery.id,
                    name: dataQuery.name,
                    image: dataQuery.image,
                },
                userToken,
            );

            setChatClient(client);
        };

        initChat();
    }, []);

    if (!chatClient) {
        return <LoadingIndicator />;
    }

    return (
        <Chat client={chatClient} theme='messaging light'>
            <ChannelList filters={filters} sort={sort} showChannelSearch={true}/>
            <Channel>
                <Window>
                    <ChannelHeader />
                    <MessageList />
                    <MessageInput />
                </Window>
                <Thread />
            </Channel>
        </Chat>
    );
};

export default App;